:root {
  --primary-color: #1a1b21;
}
* {
  box-sizing: border-box;
  margin: 0;
}
body {
}

.container {
  margin: 0 auto;
  background-color: #23252c;
  width: 550px;
  padding: 44px 0;

  .main {
    margin: 0 auto;
    width: 317px;
    background: var(--primary-color);
    border-radius: 25px;
  }
}

.info-container {
  img {
    // margin-top: 44px;
    width: 317px;
    height: 317px;
    object-fit: cover;
    object-position: 0 10%;
    border-top-right-radius: 25px;
    border-top-left-radius: 25px;
  }

  h2 {
    margin-top: 21px;
    color: white;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    text-align: center;
  }
  p {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 12.8px;
    line-height: 15px;
    text-align: center;
    color: #f3bf99;
    margin-top: 6px;
  }
  h6 {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 10.24px;
    line-height: 150%;
    text-align: center;
    color: #f5f5f5;
    margin-top: 9px;
  }
  .button {
    width: 100%;
    display: flex;
    justify-content: center;

    button {
      margin: 0 1em;
      margin-top: 15px;
      width: 115px;
      height: 34px;
      border-radius: 6px;
      cursor: pointer;
    }
  }
}

button.mail {
  border: 1px solid #d1d5db;
  background-color: white;
}

button.linkedin {
  background: #5093e2;
  border: 1px solid #5093e2;
  color: white;
}

.about,
.intrest {
  width: 317px;
  background: var(--primary-color);
  color: white;
  margin: 0 auto;
  width: 247px;

  h3 {
    padding-top: 15px;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
  }
  p {
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 10.24px;
    line-height: 150%;
    color: #dcdcdc;
  }
}

.footer {
  height: 64px;
  margin: 0 auto;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #161619;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;

  img {
    margin: 0 1em;
    cursor: pointer;
  }
}
